.gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(450px, 1fr)); /* Variable width, with a minimum of 500px */
    gap: 50px;
    margin: 50px;
    font-family: "ivypresto-display", serif;
    font-weight: 300;
    font-style: normal;
    font-size: 30px;
  }
  
  .gallery-item {
    position: relative;
    overflow: hidden;
  }
  
  .gallery-item a {
    display: block;
    position: relative;
    height: 100%; /* Ensure the link takes up full height */
  }
  
  .gallery img {
    width: 100%; /* Ensure image takes up full width */
    height: auto; /* Allow the height to adjust based on image aspect ratio */
    object-fit: contain; /* Ensure the image fits fully within the container */
    transition: all 0.3s ease;
  }
  
  .gallery-item:hover img {
    opacity: 0.7; /* Darken the image on hover */
  }
  
  .gallery-item figcaption {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: #000000;
    background-color: rgba(255, 255, 255, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "ivypresto-display", serif;
    font-weight: 300;
    font-style: normal;
    font-size: 20px;
    text-align: center;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .gallery-item:hover figcaption {
    opacity: 1; /* Show the caption on hover */
  }
  